body.home,
body.single-tours {
	$header-height: 92px;

	#site-header {
		position: relative;
		z-index: 99;

		.logo-dark { display: none; }

		&.sticky {
			.logo-dark { display: inline-block; }
			.logo-white { display: none; }
		}
	}

	#site-header.sticky.sticky-fixed { position: fixed; }
	#site-header.sticky.sticky-sticky { position: sticky; }

	#site-header:not(.sticky) {
		background-color: rgba(255, 255, 255, 0);

		#menu-main-desktop a { color: $white; }

		svg,
		button {
			color: $white;
			transition: color 0.3s ease;
		}

		a,
		button {
			&:hover svg,
			&:focus svg {
				color: $green;
			}
		}
	}

	.marquee {
		margin-top: - $header-height;
		padding-top: $header-height;
	}
}
