//
// Wrapper for all page contents, including shelf
//

#app-stage { box-shadow: 0 0 10px 1px $gray-700; }

.stage {
	background-color: $stage-bg;
	position: relative;
	min-height: 100vh;
}

//
// Shelf contents that is shown/hidden
//

.stage-shelf {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	z-index: 0;
	width: $stage-shelf-width;
	padding: $spacer-article ($spacer-base * 4);
	background-color: $gray-800;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	color: $white;
	letter-spacing: 1.5px;

	a {
		text-transform: uppercase;
		font-size: $font-size-sm;
	}

	a,
	.search-modal-toggle {
		color: rgba(255, 255, 255, 0.5);
		transition: 0.3s ease;

		&:focus,
		&:hover {
			color: $white;
		}
	}

	.stage-toggle {
		color: rgba(255, 255, 255, 0.5);
		position: absolute;
		top: calc(#{$spacer-article} - 3px);
		right: $spacer-base * 4;
		transition: 0.3s ease;

		svg { width: 13px; }

		&:hover,
		&:focus { color: $white; }
	}
}

.stage-shelf-right {
	left: auto;
	right: 0;
}

//
// Button-based toggle for showing/hiding the stage shelf
//

.stage-toggle {
	cursor: pointer;
	font-size: 30px;
	border: none;
	background-color: rgba(0, 0, 0, 0);
	color: $gray-800;
	position: relative;
	z-index: 2;
	padding: 5px 10px;
	line-height: 1;

	.svg-inline--fa {
		width: 0.65em;
	}

	$border-thickness: 2px;

	span[class^='stage-toggle-border'] {
		position: absolute;
		display: block;
		background-color: $green-light;
	}

	.stage-toggle-border-left {
		bottom: 0;
		left: 0;
		width: $border-thickness;
		height: 0;
	}

	.stage-toggle-border-top {
		top: 0;
		left: 0;
		width: 0;
		height: $border-thickness;
	}

	.stage-toggle-border-right {
		top: 0;
		right: 0;
		width: $border-thickness;
		height: 0;
	}

	.stage-toggle-border-bottom {
		bottom: 0;
		right: 0;
		width: 0;
		height: $border-thickness;
	}

	&:hover,
	&:focus {
		outline: none;

		.stage-toggle-border-left { animation: draw-vertical 0.1s linear forwards; }
		.stage-toggle-border-top { animation: draw-horizontal 0.1s linear 0.1s forwards; }
		.stage-toggle-border-right { animation: draw-vertical 0.1s linear 0.2s forwards; }
		.stage-toggle-border-bottom { animation: draw-horizontal 0.1s linear 0.3s forwards; }
	}
}

@keyframes draw-vertical {
	from { height: 0; }
	to { height: 100%; }
}

@keyframes draw-horizontal {
	from { width: 0; }
	to { width: 100%; }
}
