.nav-secondary-wrapper { margin: ($spacer-base * 4.5) 0; }
.nav-secondary-wrapper.nav-secondary-tour-wrapper { margin: 0; }

.nav-secondary {
	border-top: 1px solid $gray-200;
	border-bottom: 1px solid $gray-200;
	padding: 0;
	transition: color 0.3s ease, background 0.3s ease;
	background-color: $white;

	&:hover,
	&:focus {
		background-color: $green;

		button[data-toggle="collapse"] { color: $white; }

		.nav-secondary-items-wrapper {
			background-color: $white;
		}
	}

	button[data-toggle="collapse"],
	.nav-secondary-items span,
	.nav-secondary-items a {
		transition: color 0.3s ease-in-out-circ, background 0.3s ease-in-out-circ;
		font-size: 11px;
		letter-spacing: 1px;
		text-transform: uppercase;
	}

	button[data-toggle="collapse"] {
		background-color: $transparent;
		border: none;
		padding: 12px 0;
		cursor: pointer;
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;

		svg { margin-top: -2px; }

		&:hover,
		&:focus { outline: none; }
	}

	.nav-secondary-items-wrapper {
		display: block;
		width: 100%;
	}

	.nav-secondary-items {
		span,
		a {
			display: block;
			padding: 12px 0;
			cursor: pointer;
			color: $black;

			&:hover,
			&:focus {
				text-decoration: none;
				background-color: $green;
				color: $white;
			}
		}
	}

	@include media-breakpoint-up('xl') {
		&:hover,
		&:focus { background-color: $white; }

		button[data-toggle="collapse"] { display: none; }

		.nav-secondary-items {
			display: flex !important;
			justify-content: center;
			max-width: 1088px;
			margin: 0 auto;

			a,
			span { padding: 12px 15px; }
		}
	}
}

.marquee + .nav-secondary-wrapper { margin-top: -2px; } // to counter act the 2px of dom elements put by Sticky.js
