.btn,
input[type="button"] {
	padding: $btn-padding;
	cursor: pointer;
	border-radius: $btn-border-radius;
	text-transform: uppercase;
	font-size: $font-size-sm;
	border-style: solid;

	&,
	+ .fa { transition: border 0.3s ease-in-out-circ, color 0.3s ease-in-out-circ, background 0.3s ease-in-out-circ !important; }

	&.btn-primary,
	&[type="button"] {
		color: $white;
		background-color: $color-primary;
		border-color: $color-primary;

		&:hover,
		&:focus {
			background-color: darken($color-primary, 15%);
			color: $white;
			border-color: darken($color-primary, 15%);
		}

		+ .fa { color: $white; }

		&:disabled {
			background-color: $gray-600;
			border-color: $gray-600;
		}
	}

	&.btn-primary-i {
		background-color: rgba(0, 0, 0, 0);
		color: $color-primary;
		border-color: $color-primary;

		&:hover,
		&:focus {
			background-color: $color-primary;
			border-color: $color-primary;
			color: $white;

			+ .fa { color: $white; }
		}

		+ .fa { color: $color-primary; }
	}

	&.btn-secondary {
		background-color: $color-secondary;
		color: $white;
		border-color: $color-secondary;

		&:hover,
		&:focus {
			background-color: darken($color-secondary, 15%);
			border-color: darken($color-secondary, 15%);
			color: $white;

			+ .fa { color: $white; }
		}

		+ .fa { color: $color-secondary; }
	}

	&.btn-secondary-i {
		background-color: rgba(0, 0, 0, 0);
		color: $color-secondary;
		border-color: $color-secondary;

		&:hover,
		&:focus {
			background-color: $color-secondary;
			border-color: $color-secondary;
			color: $white;

			+ .fa { color: $white; }
		}

		+ .fa { color: $color-secondary; }
	}

	&.btn-icon { padding: $btn-icon-padding; }
}

.btn-wrapper-add-arrow {
	.nf-field-element {
		position: relative;

		.fa {
			position: absolute;
			top: 0;
			bottom: 0;
			right: 26px;
			display: flex;
			align-items: center;
		}
	}
}
