.marquee {
	width: 100%;
	height: 300px;
	background-size: cover;
	background-position: center center;
	min-height: 100vh;
	color: $white;

	.marquee-content-wrapper {
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
		position: relative;
		z-index: 2;
	}

	h1 {
		text-transform: uppercase;
		margin: 0;
		color: $white;

		+ p {
			font-size: $font-size-lg;
		}
	}
}

body.not-home .marquee {
	position: relative;
	min-height: 50vh;

	@include media-breakpoint-up('lg') {
		min-height: 350px;

		&::after {
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			height: 20%;
			z-index: 1;
			content: '';
			/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#000000+5,000000+100&0+0,0.7+100 */
			background: -moz-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,0.03) 5%, rgba(0,0,0,0.7) 100%); /* FF3.6-15 */
			background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%,rgba(0,0,0,0.03) 5%,rgba(0,0,0,0.7) 100%); /* Chrome10-25,Safari5.1-6 */
			background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0.03) 5%,rgba(0,0,0,0.7) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#b3000000',GradientType=0 ); /* IE6-9 */
		}

		h1,
		p { margin-bottom: 0; }

		h1 {
			padding-right: $spacer-base;
		}

		.marquee-content-wrapper {
			text-align: left;
			justify-content: flex-start;
			align-items: flex-end;
		}

		.marquee-content {
			display: flex;
			flex-wrap: wrap;
			align-items: flex-end;
			padding-bottom: $spacer-article;

			.badge-wrapper { width: 100%; }
		}
	}
}

body.not-home.single-tours .marquee {
	min-height: 80vh;

	@include media-breakpoint-up('lg') {
		height: 820px;
		max-height: 100vh;

		.marquee-content {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			max-width: 400px;
			padding-bottom: 52px;

			.marquee-subtitle { margin-top: $spacer-base; }
		}
	}
}

@keyframes scroll {
	0% {
		opacity: 0;
		transform: translateY(-15%);
	}

	15% {
		opacity: 1;
	}

	60% {
		opacity: 1;
	}

	100% {
		opacity: 0;
		transform: translateY(15%);
	}
}

body.home .marquee {
	min-height: 476px;
	height: 100vh;

	h1 + p {
		margin-top: $spacer-base;
	}

	&.marquee-text-position-top-right .marquee-content-wrapper {
		justify-content: flex-end;
		text-align: right;
		align-items: flex-start;
		padding-top: $spacer-section;
	}

	&.marquee-text-position-top-left .marquee-content-wrapper {
		padding-top: $spacer-section;
		justify-content: flex-start;
		align-items: flex-start;
		text-align: left;
	}

	&.marquee-text-position-middle-left .marquee-content-wrapper {
		justify-content: flex-start;
		text-align: left;
	}

	&.marquee-text-position-bottom-left .marquee-content-wrapper {
		text-align: left;
		justify-content: flex-start;
		align-items: flex-end;
		padding-bottom: $spacer-section * 2;
	}

	&.marquee-text-position-top-middle .marquee-content-wrapper {
		padding-top: $spacer-section;
		align-items: flex-start;
	}

	// &.marquee-text-position-middle-middle .marquee-content-wrapper {} //at time of dev, this is the default inherited style.

	&.marquee-text-position-bottom-middle .marquee-content-wrapper {
		padding-bottom: $spacer-section * 2;
		align-items: flex-end;
	}

	&.marquee-text-position-middle-right .marquee-content-wrapper {
		justify-content: flex-end;
		text-align: right;
	}

	&.marquee-text-position-bottom-right .marquee-content-wrapper {
		justify-content: flex-end;
		text-align: right;
		align-items: flex-end;
		padding-bottom: $spacer-section * 2;
	}

	@include media-breakpoint-up('lg') {
		min-height: 720px;

		.marquee-content {
			max-width: 520px;
		}
	}
}

.marquee-hint {
	position: absolute;
	bottom: $spacer-article;
	left: 0;
	right: 0;
	margin: auto;
	display: flex;
	flex-direction: column;
	align-items: center;
	color: $white;
	z-index: 2;

	span {
		text-transform: uppercase;
		font-size: 12px;
		font-weight: 600;
		margin-bottom: $spacer-base * 0.75;
	}

	svg {
		fill: currentColor;
		animation: scroll 4s ease infinite;
	}
}
