@mixin full-width (
	$mobile_margin: 15px,
	$sm_parent_width: map-get($container-max-widths, 'sm'),
	$md_parent_width: map-get($container-max-widths, 'md'),
	$lg_parent_width: map-get($container-max-widths, 'lg'),
	$xl_parent_width: map-get($container-max-widths, 'xl'),
	$xxl_parent_width: map-get($container-max-widths, 'xxl')
) {
	margin-left: -$mobile_margin;
	margin-right: -$mobile_margin;

	@media (min-width: map-get($grid-breakpoints, 'sm')) {
		margin-left: calc(-100vw / 2 + #{$sm_parent_width - $mobile_margin} / 2);
		margin-right: calc(-100vw / 2 + #{$sm_parent_width - $mobile_margin} / 2);
	}

	@media (min-width: map-get($grid-breakpoints, 'md')) {
		margin-left: calc(-100vw / 2 + #{$md_parent_width - $mobile_margin} / 2);
		margin-right: calc(-100vw / 2 + #{$md_parent_width - $mobile_margin} / 2);
	}

	@media (min-width: map-get($grid-breakpoints, 'lg')) {
		margin-left: calc(-100vw / 2 + #{$lg_parent_width - $mobile_margin} / 2);
		margin-right: calc(-100vw / 2 + #{$lg_parent_width - $mobile_margin} / 2);
	}

	@media (min-width: map-get($grid-breakpoints, 'xl')) {
		margin-left: calc(-100vw / 2 + #{$xl_parent_width - $mobile_margin} / 2);
		margin-right: calc(-100vw / 2 + #{$xl_parent_width - $mobile_margin} / 2);
	}

	@media (min-width: map-get($grid-breakpoints, 'xxl')) {
		margin-left: calc(-100vw / 2 + #{$xxl_parent_width - $mobile_margin} / 2);
		margin-right: calc(-100vw / 2 + #{$xxl_parent_width - $mobile_margin} / 2);
	}
}

@mixin hover-border-bottom ($color, $vpadding, $hpadding) {
	position: relative;
	border: none;
	width: 100%;
	color: $color;
	padding: $vpadding $hpadding;

	&::after {
		content: '';
		position: absolute;
		bottom: 2px;
		left: 0;
		right: 0;
		margin: auto;
		transition: width 0.3s ease;
		background-color: $color;
		width: 0;
		height: 3px;
	}

	&:hover,
	&:focus {
		text-decoration: none;

		&::after { width: calc(100% - #{$hpadding} * 2); }
	}
}

@mixin subheading-weight($weight) {
	+ h1,
	+ .h1,
	+ h2,
	+ .h2,
	+ h3,
	+ .h3,
	+ h4,
	+ .h4,
	+ h5,
	+ .h5,
	+ h6,
	+ .h6 { font-weight: $weight; }
}

// assumes the use of bootstrap variables
@mixin font-responsive () {
	body { font-size: responsive; }

	main {
		font-range: $font-range-low $font-range-high;
		font-size: responsive $font-size-base-low $font-size-base-high;
	}

	.lead {
		font-range: $font-range-low $font-range-high;
		font-size: responsive $lead-font-size-sm $lead-font-size;
	}

	h1,
	.h1 {
		font-range: $font-range-low $font-range-high;
		font-size: responsive $h1-font-size-low $h1-font-size;

		// @include subheading-weight(300);
	}

	h2,
	.h2 {
		font-range: $font-range-low $font-range-high;
		font-size: responsive $h2-font-size-low $h2-font-size;

		// @include subheading-weight(300);
	}

	h3,
	.h3 {
		font-range: $font-range-low $font-range-high;
		font-size: responsive $h3-font-size-low $h3-font-size;

		// @include subheading-weight(300);
	}

	h4,
	.h4 {
		font-range: $font-range-low $font-range-high;
		font-size: responsive $h4-font-size-low $h4-font-size;

		// @include subheading-weight(300);
	}

	h5,
	.h5 {
		font-range: $font-range-low $font-range-high;
		font-size: responsive $h5-font-size-low $h5-font-size;

		// @include subheading-weight(300);
	}

	h6,
	.h6 {
		font-range: $font-range-low $font-range-high;
		font-size: responsive $h6-font-size-low $h6-font-size;
		text-transform: uppercase;

		// @include subheading-weight(300);
	}
}
