#menu-header-desktop {
	display: flex;
	justify-content: center;
	text-transform: uppercase;
	font-size: $font-size-sm;
	font-weight: 600;
	margin-left: -15px;
	margin-right: -15px;

	a {
		color: rebeccapurple;
		transition: color 0.3s ease-in-out-circ;
		padding: 0 15px;

		&:hover,
		&:focus,
		&:active,
		&.active { color: $blue; }
	}

	.menu-item-has-children {
		position: relative;

		> a {
			position: relative;

			&::after {
				position: absolute;
				transform: scale(0.75);
				left: 0;
				right: 0;
				bottom: -8px;
				margin: auto;
			}
		}

		> ul.show {
			position: absolute;
		}
	}
}

#menu-buttons-desktop {
	display: flex;
	justify-content: flex-end;
	flex-wrap: nowrap;

	li {
		margin-right: 30px;

		a {
			border-width: 1px;
			border-color: $blue;
			border-style: solid;
			font-size: calc(#{$font-size-base} - 1px);
			padding: $btn-padding;
			border-radius: $btn-border-radius;
			color: $white;
			background-color: $blue;
			transition: background 0.3s ease-in-out-circ, color 0.3s ease-in-out-circ, border 0.3s ease-in-out-circ;

			&:hover {
				background-color: darken($blue, 15%);
				border-color: darken($blue, 15%);
			}
		}

		&:last-of-type { margin-right: 0; }

		&:first-of-type a {
			background-color: rgba(0, 0, 0, 0);
			color: rebeccapurple;
			border-color: rebeccapurple;

			&:hover {
				background-color: rebeccapurple;
				color: $white;
			}
		}
	}
}

.navbar {
	padding: 10px 0;
}
