img {
	max-width: 100%;
}

#html, { margin-top: 0 !important; }
#tinymce { margin: 20px; }

body.admin-bar { padding-bottom: 46px; }

body {
	@media (min-width: 1440px) {
		&#tinymce { background-color: #fff; }
	}
}

.badge {
	text-transform: uppercase;
	letter-spacing: 1px;
	font-weight: 400;
	padding: 6px 10px;
	font-size: 11px;
}

.first-letter {
	font-family: $headings-font-family;
	font-size: responsive 74px 82px;
	font-range: $font-range-low $font-range-high;
	color: $black;
	font-weight: 600;
	line-height: 1;
	float: left;
	margin-top: -10px;
	margin-bottom: -10px;
	padding-right: 5px;
}

body.not-home .logo-white { display: none; }

body.not-home.single-tours {
	.logo-white { display: block; }
	.logo-dark { display: none; }
}

.main {
	margin-top: $spacer-section + ($spacer-base * 2);
	margin-bottom: $spacer-section-lg;

	> section {
		margin-bottom: $spacer-section-lg;

		&.section-no-margin-bottom {
			margin-bottom: 0;

			&:last-of-type { margin-bottom: -$spacer-section-lg; }
		}

		&.section-no-margin-top {
			margin-top: 0;

			&:first-of-type { margin-top: -$spacer-section-lg; }
		}
	}

	a {
		transition: color 0.3s ease;

		&:hover,
		&:focus { color: darken($color-primary, 10%); }
	}
}

section + section { margin-top: $spacer-section-lg; }

.green { color: $green !important; }

.clearfix {
	display: inline-block;

	&::after {
		visibility: hidden;
		display: block;
		font-size: 0;
		content: " ";
		clear: both;
		height: 0;
	}
}

* html .clearfix { height: 1%; }
.clearfix { display: block; }

.site-title {
	text-transform: uppercase;
	font-weight: 600;
	font-family: $headings-font-family;
	font-size: 18px;
	letter-spacing: 0;
}

@include media-breakpoint-up('lg') {
	.container {
		padding-left: $spacer-section;
		padding-right: $spacer-section;
	}
}

@include media-breakpoint-up('xl') {
	.nav-secondary-items {
		.container {
			padding-left: $spacer-base * 1.5;
			padding-right: $spacer-base * 1.5;
		}
	}
}

@media (min-width: 1400px) {
	.container {
		padding-left: $spacer-base * 1.5;
		padding-right: $spacer-base * 1.5;
	}
}
