@include font-responsive();

body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

p + h1,
p + h2,
p + h3,
p + h4,
p + h5,
p + h6 { margin-top: 2rem; }

h1.text-center,
h2.text-center,
h3.text-center,
h4.text-center,
h5.text-center,
h6.text-center {
	max-width: 860px;
	margin-left: auto;
	margin-right: auto;
}

h1,
h2,
h4,
h5,
h6 { text-transform: uppercase; }

.subheading {
	margin-top: $spacer-base;
	margin-bottom: $spacer-article;
	color: $gray-600;

	&.text-center {
		max-width: 900px;
		margin-left: auto;
		margin-right: auto;
	}
}

.main p.small,
.small { line-height: 1.636363636; }

h5 {
	font-family: $font-family-base;
	text-transform: uppercase;
	font-size: 11px;
	letter-spacing: 1px;
	line-height: 17px;
	color: $gray-600;
}

.main {
	p {
		line-height: 2;
	}

	h2,
	h3,
	h4 {
		&,
		a { color: $gray-800; }

		a:hover,
		a:focus {
			text-decoration: none;
			color: $color-primary;
		}
	}
}

h2 { display: none !important; }
